@import url(https://fonts.google.com/specimen/Comfortaa?query=com&preview.text_type=custom&sidebar.open=true&selection.family=Comfortaa:wght@300;400;500;600;700);
@import url(https://fonts.google.com/specimen/Spartan?preview.text_type=custom&sidebar.open=true&selection.family=Spartan:wght@200;300;400);
*, body{
    margin:0;
    padding:0;
    font-family: 'Comfortaa' ,sans-serif;
}

li{
    list-style-type:none;
}

a:link, a:visited, a:hover, a:focus{
    text-decoration:none; 
}

img{
    width:100%;
}

button{
    outline: none;
}

.App{
    width:100%;
}

.btn{
    border-radius: 20px;
    color:#fff;
    background-color:#0BBDC8;
    text-align: center;
    cursor: pointer;
}

.clossBox{
    position:fixed;
    top:80px;
    left:0;
    width:100vw;
    height:100vh;
    background-color:rgba(0,0,0,0);
    z-index: 10;
}

.modalBox{
    padding:20px 40px;
    background-color:#fff;
    min-width:364px;
    border-radius: 56px;
    position:fixed;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    box-shadow:0 0 5px 8px rgba(0,0,0,.1);
    z-index:200;
}

.modal .clossBox{
    background-color:rgba(0,0,0,.2);
    top:0;
    z-index:100;
}

@media only screen and (max-width: 500px){
    .modalBox{
        min-width:65%;
    }

    .modal_text{
        margin: 18px auto 30px;
    }
}
#header{
    width:100%;
    background-color:rgba(0,0,0,0);
    position:fixed;
    top:0;
    left:0;
    z-index: 100;
}

#header:hover, #header.hover{
    background-color:#fff !important;
}

.header_wrap{
    padding:15px 80px;
    display:flex;
    justify-content: space-between;
}

h1{
    width:114px;
}

.header_center{
    display:flex;
    flex:1 1;
    justify-content: flex-end;
}

.header_center li{
    position:relative;
    width:140px;
    margin-left:32px;
    cursor: pointer;
}

.nav{
    display:block;
    width:100%;
    font-weight: bold;
    font-size: 12px;
    line-height: 40px;
    color: #1C1C1C;
    text-align: center;
}     

.nav.on, .nav:hover{
    color: #02BDC8;
}

.header_right{
    display: flex;
    margin-left:15vw;
}

.header_right p{
    line-height: 40px;
    margin-left:38px;
    height:40px;
}

.header_right a{
    font-weight: bold;
    font-size: 12px;
    color: #1C1C1C;
}

.talk_to_sales{
    border-radius: 20px;
    padding:0 20px;
    text-align: center;
    background-color:#F3E5D6;
}

#header:hover .talk_to_sales{
    background-color:#000;
}

#header:hover .talk_to_sales a{
    color:#fff;
}


.navListBox{
    background-color:#fff;
    box-shadow: 3px 4px 10px rgba(0, 0, 0, 0.15);
    border-radius: 0px 0px 40px 40px;
    position:absolute;
    top:40px;
    left:0;
    width:100%;
    z-index: 10;
}

.navListBox a{
    display:block;
    font-family: Spartan;
    font-weight: 500;
    font-size: 12px;
    line-height: 40px;
    text-align: center;
    color: #B7B7B7;
    cursor: pointer;
}

.navListBox a.on,.navListBox a:hover{
    color: #02BDC8;
}

.header_mobile{display:none}

@media only screen and (max-width: 1350px){
    .header_right {
        margin-left: 40px;
    }
}

@media only screen and (max-width: 1200px){
    .header_right,
    .header_center li{
        margin-left: 0px;
    }
}

@media only screen and (max-width: 1025px){
    h1{
        width:100px;
    }

    .header_center li{
        width:90px;
    }

    .header_right p{
        margin-left:10px;
    }
}

@media only screen and (max-width: 830px){
    .header_wrap{display:none}

    .header_mobile{
        display:block;
    }
    
    .header_mobile_bar{
        display:flex;
        justify-content: space-between;
        background-color:#fff;
        position:fixed;
        padding:5px 0;
        top:0;
        left:0;
        z-index: 100;
        width:100%;
    }

    h1{
        margin-top: 8px;
        padding-left:80px;
    }

    .mobileNav{
        padding-right:53px;
    }

    .mobile_navbox{
        position:absolute;
        top:58px;
        left:0;
        background: #FFFFFF;
        border-radius: 0px 0px 50px 50px;
        width:100%;
        padding:30px 0;
        z-index:2;
    }

    .header_center{
        display:block;
        width:100%;
        margin:0 auto;
    }

     .header_right{
        display:block;
        width:80%;
        margin:55px auto 0;
    }

    .header_center li,
    .header_right p{
        text-align: center;
        width:100%;
        margin-left:0;
    }

    .navListBox_mobile{
        background: #FBFBFB;
        width:100%;
        padding:10px 0;
        height:1;
        transition: height 0.3s;
    }

    .navListBox_mobile.on{
        height:auto;
    }

    .navListBox_mobile a{
        display:block;
        font-family: Spartan;
        font-weight: 500;
        font-size: 12px;
        line-height: 40px;
        text-align: center;
        color: #1C1C1C;
        cursor: pointer;
    }

    .talk_to_sales{
        padding:0;
        background-color:#000;
        margin-top:20px;
    }

    .talk_to_sales a{
        color:#fff;
    }

    #header .clossBox{
        z-index: 0;
        background-color:rgba(0,0,0,.2);
    }
}

@media only screen and (max-width: 767px){
    h1{
        padding-left:50px;
    }

    .mobileNav{
        padding-right:33px;
    }
}

@media only screen and (max-width: 468px){
    .header_mobile_bar{
        padding:0;
    }

    h1{
        margin-top: 10px;
        padding-left:20px;
    }

    .mobileNav{
        padding-right:0;
    }

    .header_right{
        width:253px;
    }

    .nav,
    .navListBox_mobile a{
        font-size:11px;
    }
}
#footer{
    width:100%;
    height:397px;
    background-color:#fff;
}

.footer_top{
    padding:73px 126px 0;
    display:flex;
    justify-content: space-around;
}

.footer_top div, .footer_logo{
    min-width:20%;
    max-width:20%;
    flex:1 1;
}

.footer_logo span{
    display:inline-block;
    width:37px;
} 

.footer_top_title{
    font-family: Spartan;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    color: #1C1C1C;
}



.footer_top li{
    margin-top:27px;
    cursor: pointer;
}

.footer_top li a{
    font-family: Spartan;
    font-size: 12px;
    color:#000;
}

.footer_top li a:hover{
    color:#02BDC8;
}

.footer_bottom{
    padding:95px 126px 0;
    display:flex;
    justify-content: space-around;
}

.footer_bottom p{
    font-family: Spartan;
    font-size: 12px;
    margin-top:27px;
    min-width:20%;
    max-width:20%;
    flex:1 1;
}

.footer_bottom p a{
    font-family: Spartan;
    font-size: 12px;
    color:#000;
}

.footer_sns a{
    display:inline-block;
    margin-right:23px;
}

.footer_sns a:last-child{
    margin-right:0;
}

.footer_sns_mobile{
    display:none;
}

@media only screen and (max-width: 1200px){
    #footer{
        padding-bottom:20px;
    }

    .footer_top,
    .footer_bottom{
        display:block;
        padding:55px 80px 0;
        overflow: hidden;
    }

    .footer_logo{
        margin-bottom:30px;
    }

    .footer_top div,
    .footer_bottom p{
        float:left;
        width:25%;
        max-width:25%;
        min-width:25%;
    }

    .footer_top li{
        margin-top:18px;
    }

    .footer_none{
        display:none;
    }

}

@media only screen and (max-width: 767px){
    .footer_top div,
    .footer_bottom p{
        width:50%;
        max-width:50%;
        min-width:50%;
    }

    .footer_top div{
        height:120px;
        margin-bottom:50px;
    }

    .footer_bottom{
        padding-top:0;
        padding-bottom:20px;
    }
}

@media only screen and (max-width: 500px){
    .footer_bottom{
        display:none;
    }

    .footer_bottom.footer_sns_mobile{
        display:block;
        text-align: center;
    }

    .footer_bottom p{
        width:100%;
        max-width: 100%;
        min-width: 100%;
        float:none;
    }

     .footer_bottom p.footer_sns{
        text-align: center;
        margin-top:0;
    }

    .mobile_link{
        display:flex;
        justify-content: space-between;
    }

    .footer_logo span{
        width:25px;
    }

    .footer_top li{
        margin-top:15px;
        font-size:11px;
    }

    .footer_top_title,
    .footer_bottom p,
    .footer_bottom p a{
        font-size:11px;
    }
}

@media only screen and (max-width: 468px){
     .footer_top{
        padding:30px 30px 0;
    }

    .footer_bottom.footer_sns_mobile{
        padding:0 45px 30px;
    }
   
}
#main{
    width:100%;
}

.main_setcion1{
    position:relative;
    width:100%;
    height:852px;
    background-color:#F3E5D6;
    overflow: hidden;
}

.main_section1_textArea{
    position:absolute;
    top:240px;
    left:80px;
    z-index: 1;
}

.main_setcion1 .title{
    font-weight: 300;
    font-size: 56px;
    line-height: 85px;
    letter-spacing: -0.01em;
    color: #000000;
    margin-bottom:20px;
}

.main_setcion1 .desc{
    font-weight: 300;
    font-size: 17px;
    line-height: 33px;
    letter-spacing: -0.01em;
    color: #000000;
    word-break: keep-all;
    width:520px;
}

.main_setcion1 .desc span{
    display:block;
    line-height: 33px;
}

.main_section1_imgArea{
    position:absolute;
    right: -86px;
    bottom: 0;
    height:100%;
    width:100%;
    background:url(/static/media/Asset44.b0917716.png) no-repeat bottom right;
    background-size:auto 100%;
}

.main_section1_imgArea p{
    position:relative;
    width:100%;
    height:100%;
}

.italic{
    display:block;
    width:563px;
    position:absolute;
    right: 10%;
    bottom: -5px;
}

.main_setcion2 .title{
    font-weight: 300;
    font-size: 45px;
    line-height: 85px;
    letter-spacing: -0.01em;
    color: #000000;
    text-align: center;
    margin-top:20px;
}

.section2_navs{
    display:flex;
    justify-content: space-between;
    width:624px;
    margin:10px auto 54px;

}

.section2_nav{
    text-align: center;
    letter-spacing: -0.01em;
    color: #B7B7B7;
    cursor: pointer;
    line-height:32px;
    padding:0 3px;
}

.section2_nav span{
    font-weight: normal;
    font-size: 15px;
}

.section2_nav.on span{
    color: #1C1C1C;
    border-bottom:2px solid rgba(2, 189, 200, 1)
}

.main_section2_img{
    height:733px;
}

.main_section2_img p{
    text-align: center;
}

.main_section2_img i{
    display:inline-block;
    height:495px;
    text-align: center;
}

.main_section2_img img{
    width:auto;
    height:100%;
}

.main_section2_img span{
    font-weight: normal;
    font-size: 14px;
    letter-spacing: -0.01em;
    color: #B7B7B7;
    display:block;
    text-align: center;
    margin-top:20px;
}

.main_section2_botton{
    display:flex;
    justify-content: space-between;
    padding:0 300px 79px;
}

.main_section2_botton .main_img{
    height:34px;
    text-align: center;
}

.main_section2_botton img{
    width:auto;
    height:100%;
}

.main_section2_botton .percent{
    font-family: Spartan;
    font-weight: bold;
    font-size: 39px;
    line-height: 85px;
    letter-spacing: -0.01em;
    color: #000000;
    margin:45px auto 5px;
    text-align: center;
}

.main_section2_botton .text{
    font-size: 13px;
    text-align: center;
    letter-spacing: -0.01em;
    color: #000000;
}

.main_setcion3{
    height:340px;
    background: linear-gradient(90deg, #02BDC8 0%, #00A3D6 100%);
}

.main_setcion3 .title{
    font-weight: 300;
    font-size: 35px;
    line-height: 85px;
    text-align: center;
    letter-spacing: -0.01em;
    color: #FFFFFF;
    padding: 78px 0 33px;
}

.main_setcion3 .radius{
    font-family: Spartan;
    font-weight: bold;
    font-size: 12px;
    line-height: 45px;
    height:45px;
    text-align: center;
    color: #FFFFFF;
    border-radius:45px;
    border:1px solid #fff;
    background-color:rgba(0,0,0,0);
    width: 150px;
    margin:0 auto;
}

.mobile{
    display:none;
}

.pc{
    display:block;
}

@media only screen and (max-width: 1000px){
    .mobile{
        display:block;
    }

    .pc{
        display:none;
    }

    .main_setcion1{
        height:640px;
        background-size:auto 60%;
    }

    .main_section1_textArea {
        top:150px;
        margin:0 auto;
        width: 513px;
    }

    .main_setcion1 .title, 
    .main_setcion2 .title{
        font-size:45px;
        line-height: 61px;
    }

    .main_section1_imgArea {
        height: 76%;
    }

    .italic {
        width: 300px;
    }

    .main_section2_botton{
        padding:0 80px 79px;
    }
}

@media only screen and (max-width: 767px){
    .main_setcion1{
        height:640px;
    }

    .main_section1_textArea {
        width:100%;
        left:30px;
    }

    .main_section1_textArea .desc{
        width:263px;
        font-size: 12px;
        line-height: 21px;
    }

    .main_setcion1 .title,
    .main_setcion2 .title{
        font-size: 30px;
        line-height: 45px;
    }

    .main_setcion2 .title{
        margin:20px 0;
    }

    .main_section1_imgArea {
        height: 66%;
    }

    .italic {
        width: 300px;
    }

    .section2_navs{
        display:grid;
        width:100%;
        grid-template-columns: repeat(2, 1fr);
    }

    .section2_nav{
        margin-bottom:20px;
    }

    .main_section2_botton{
        display:block;
        padding:0;
        margin-top:130px;
    }

    .main_section2_botton li{
        margin-bottom:65px;
    }

    .main_section2_botton .percent{
        font-size: 33px;
        line-height: 85px;
        margin-top:0;
    }

    .main_section2_botton  .text{
        font-size: 12px;
    }

    .main_section2_img{
        padding:0 30px;
        height:auto;
    }

    .main_section2_img i{
        height:auto;
    }

    .main_section2_img img {
        width:100%;
        height:auto;
    }

    .main_setcion3 .title{
        font-size: 33px;
    }
    
}


@media only screen and (max-width: 468px){
    .main_section1_textArea {
        top: 105px;
    }

    .main_section1_imgArea {
        right: 0;
        width: 100%;
        height:74%;
        background-size: auto 91%;
        background-position: 55px 42px;
    }

    .italic {
        width: 200px;
        left:50%;
        transform: translateX(-50%);
    }

    .main_section2_img span{
        font-size: 12px;
        line-height: 20px;
    }

    .section2_nav span{
        font-size: 12px;
    }

    .main_setcion2 .title{
        font-size: 30px;
    }

    .main_setcion3 .title{
        font-size: 22px;
    }
}
#products{
    width:100%;
}

.products_setcion1{
    display:flex;
    flex-direction: row-reverse;
    height:847px;
    background:#fff url(/static/media/shape1.e7c73410.png) no-repeat;
    background-size:auto 103.5%;
    overflow: hidden;
    background-position:top -30px right -30px;
}

.products_section1_textArea{
    position:relative;
    margin-left:80px;
    width:30%;
}

#products .texts{
    position:absolute;
    top:50%;
    left:0;
    transform: translateY(-50%);
    margin-left:0;
}

.products_title span{
    font-weight: 300;
    font-size: 56px;
    line-height: 69px;
    letter-spacing: -0.01em;
    color: #000000;
    display:block;
}

.products_title span.sub{
    font-size: 36px;
    margin:20px 0;
}

.desc span{
    display:block;
    font-size: 17px;
    line-height: 33px;
    letter-spacing: -0.01em;
}

.products_section1_img{
    flex:1 1;
    /* display: flex; */
    justify-content: space-around;
    position:relative;
    margin-right:80px;
}

.product_1, 
.product_2,
.product_3{
    position:absolute;
    top:50%;
    transform: translateY(-50%);
}

.product_1{
    right:36%;
    width: 40%;
}

.product_2{
    top:41%;
    right:10%;
    transform: translate(-50%, -50%);
    width:17%;
}

.product_3{
    top:64%;
    right:0;
    width:17%;
}

.products_setcion2{
    display:flex;
    flex-direction: row-reverse;
    background:#F3E5D6 url(/static/media/shape2.0eef5214.png) no-repeat;
    background-size:auto 100%;
    background-position:bottom 0 left -89px;
    height:852px;
}

.products_section2_textArea{
    width:40%;
}

.products_section2_img{
    flex:1 1;
    position:relative;
    margin-left:80px;
}

.products_section2_img .img{
    width:18%;
    position:absolute;
    top:50%;
    transform: translateY(-50%);
}

.products_section2_img .img.product_4{
    width:45%;
    left: 35%;
}

.products_section2_img .img.product_5{
    left: 27%;
    transform: translate(-50%, -50%);
}

.products_section2_img .img.product_6{
    left:0;
}

.products_section2_textArea{
    position:relative;
    width:40%;
    height:100%;
}

.products_section2_textArea .text{
    position:absolute;
    top:50%;
    transform: translateY(-50%);
    right: 80px;
}

.products_setcion3{
    padding:0 80px;
}

.products_setcion3 .products_title{
    padding:80px 0 20px;
}

.products_setcion3 .products_title span,
.products_setcion3 .desc span{
    text-align: center;
}

.products_setcion3 .mainImg{
    width:802px;
    margin:125px auto 170px;
}

.info_bottom{
    display:flex;
    justify-content: space-around;
    width:80%;
    margin:0 auto 74px;
}

.info_bottom .img{
    height:42px;
    text-align: center;
}

.info_bottom img{
    width:auto;
    height:100%;
}

.info_bottom .title{
    font-family: comforta;
    font-weight: 400;
    font-size: 39px;
    letter-spacing: -0.01em;
    color: #000000;
    margin:45px auto 5px;
    text-align: center;
}

.info_bottom .text{
    font-size: 13px;
    text-align: center;
    letter-spacing: -0.01em;
    color: #000000;
    font-weight: 300;
}

.desc.product3_desc_mobile{
    display:none;
}

@media only screen and (max-width: 1355px){
    .products_setcion1,
    .products_setcion2{
        display:block;
        width:100%;
    }

    .products_setcion1{
        background-position:top -10vw right -10vw;
        background-size:74%;
    }

    .products_section1_img{
        height: 69%;
        background-size: auto 100%;
        width: 80%;
        margin-left: auto;
    }

    .product_3 {
        top: 65%;
        width: 15%;
    }

    .product_2 {
        top: 50%;
        width: 15%;
        right: 8%;
    }

    .product_1 {
        top: 50%;
        width: 32%;
        right: 31%;
    }   

    .products_section1_textArea{
        width:100%;
    }

    .products_section2_textArea{
        height:auto;
        width:100%;
    }

    .products_section2_textArea .text{
        right:80px;
        left:auto;
        top:80px;
        transform: translate(0,0);
    }

    .products_setcion2{
        background-position:bottom 0px left -30px;
        background-size:580px;
    }

    
    .products_section2_img{
        height: 100%;
        width: 600px;
    }

    .products_section2_img .img{
        transform: translate(0,0) !important;
        top:auto;
        bottom:80px;
    }

    .products_section2_img .img.product_6{
        left:0;
    }

    .products_section2_img .img.product_5{
        left:106px;
    }

    .products_section2_img .img.product_4{
        left: 215px;
        bottom: 13px;
    }

    .products_setcion3 .mainImg{
        width:100%;
    }
    
    .info_bottom{
        width:100%;
    }
}

@media only screen and (max-width: 900px){
    .products_setcion2 .desc,
    .products_setcion3 .desc{
        display:none;
    }

    .products_setcion2 .desc.product2_desc_mobile,
    .products_setcion3 .desc.product3_desc_mobile{
        display:block;
