.selectArea .inputBox span{
    display:block;
    flex:1 1;
}

.arrow{
    display:block;
    width:15px;
    height:5px;
    margin-right:30px;
}

.arrow.open img{
    transform: rotate(180deg);
}

.optionBox{
    width:100%;
    position:absolute;
    top:41px;
    left:0;
    z-index:100;
    background-color:#fff;
    border-radius:10px;
    box-shadow: 0 0 5px 0 rgba(0,0,0,.1);
    overflow: hidden;
}

.option{
    line-height:35px;
    font-size: 14px;
    letter-spacing: -0.01em;
    color: #1C1C1C;
    padding:0 10px;
}

.option.on{
    color:#0BBDC8;
}

/* modal */
.modal_img{
    width:29px;
    height:29px;
    margin:0 auto 16px;
}

.modal_title{
    font-family: Comfortaa;
    font-weight: bold;
    font-size: 20px;
    line-height: 22px;
    text-align: center;
}

.modal_text{
    font-family: Comfortaa;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    color: #7C7C7C;
    margin:31px auto 42px;
}

.modal_btn{
    width:100%;
    background: #1C1C1C;
    border-radius: 36.5px;
    font-family: Spartan;
    font-weight: bold;
    font-size: 12px;
    line-height: 45px;
    text-align: center;
    color: #FFFFFF;
    cursor: pointer;
}         

@media only screen and (max-width:1280px){
    .form_area{
        width:70%;
    }
}

@media only screen and (max-width:1050px){
    .form_area{
        display:block;
    }

    .from_right, 
    .from_left{
        width:100%;
    }

    .contactUs{
        height: 1119px;
    }

    .contact_btn{
        margin:50px auto;
    }
}

@media only screen and (max-width:767px){
    .form_area{
        box-shadow: none;
        background-color:rgba(0,0,0,0);
        padding:0;
        width:85%;
    }

    .contactUs_title h2{
        font-size: 40px;
        line-height: 85px;
    }

    .back1{
        width:55%;
    }

    .back2 {
        width: 35%;
        bottom: 30%;
    }

    .contactUs {
        height: 946px;
    }

    .Talk  .contactUs {
        height: 1053px !important;
    }

    .from_left h3{
        text-align: center;
    }

    .from_left li{
        width:85%;
        margin:0 auto;
    }
}

@media only screen and (max-width:468px){
    .contactUs{
        padding:100px 0;
    }

    .contactUs_title h2{
        font-size: 30px;
        line-height: 85px;
    }

    .contactUs_title span{
        font-size: 12px;
        line-height: 21px;
        margin-top:0;
    }

    .form_area{
        top:260px;
        width:87%;
    }

    .input2 {
        display:block;
    }

    .input2 .inputBox{
        width:100%;
        margin-top:15px;
    }

    .from_left h3{
        font-size: 20px;
        line-height: 69px;
    }

    .from_left li{
        font-size: 13px;
        line-height: 69px;
        width:90%;
    }
}
.terms{
    padding:80px;
}

.term_title{
    margin-bottom:98px;
    text-align: center;
}

.terms h2{
    font-family: Comfortaa;
    font-weight: 300;
    font-size: 45px;
    line-height: 69px;
    text-align: center;
    letter-spacing: -0.01em;
    margin-bottom:20px;
}

.terms .sub{
    font-family: Comfortaa;
    font-size: 15px;
    line-height: 25px;
    text-align: center;
    letter-spacing: -0.01em;
    color: #B7B7B7;
}

.info p{
    font-family: Spartan;
    font-size: 15px;
    line-height: 25px;
    letter-spacing: -0.01em;
    color: #1C1C1C;
    margin-bottom:20px;
}

.info h3{
    font-family: Spartan;
    font-weight: bold;
    font-size: 15px;
    line-height: 25px;
    letter-spacing: -0.01em;
    color: #1C1C1C;
}

.more_btn{
    cursor: pointer;
    font-family: Comfortaa;
    font-weight: bold;
    font-size: 17px;
    line-height: 25px;
    letter-spacing: -0.01em;
    color: #0BBDC8;
}

@media only screen and (max-width: 767px){
    .terms{
        padding:80px 50px;
    }

    .terms h2{
        font-size: 40px;
        line-height: 85px;
    }

    .term_title {
        margin-bottom: 75px;
    }
}

@media only screen and (max-width: 468px){
    .terms{
        padding:50px 20px;
    }

    .terms h2{
        font-size: 30px;
        line-height: 85px;
        margin-bottom:0;
    }

    .terms .sub{
        font-size: 12px;
        line-height: 21px;
    }

    .term_title {
        margin-bottom: 45px;
    }

    .info p{
        font-size: 11px;
        line-height: 20px;
    }

    .more_btn{
        font-size: 12px;
        line-height: 25px;
    }

    .info h3{
        font-size:13px;
    }
}
.login{
    background:#fff url(/static/media/welcome.6f22289f.png) no-repeat bottom left;
    background-size:auto 80%; 
    height:100vh;
}

.login.signUp{
    background:#fff url(/static/media/signup.843b3394.png) no-repeat bottom left;
    background-size:auto 80%; 
}

.login header{
    padding:0 80px;
    height:110px;
}

.login h1{
    width:114px;
    padding:38px 0;
    height:34px;
}

.login_from{
    width:400px;
    margin-left:50%;
    position:relative;
    height:800px;
}

.login_right{
    width:100%;
    position:absolute;
    top:50%;
    left:0;
    transform: translateY(-50%);
}

.login_right h2{
    font-family: Comfortaa;
    font-weight: 300;
    font-size: 45px;
    line-height: 69px;
    letter-spacing: -0.01em;
    color: #000000;
    margin-bottom:40px;
}

.descArea.right{
    text-align: right;
    font-size:13px;
}

.descArea.right:hover{
    text-decoration: underline;
}

.descArea.center{
    text-align: center;
    font-size:13px;
    font-size:600;
}

.login .contact_btn{
    width:100%;
}

.descArea.center a{
    color: #0BBDC8; 
    display:inline-block;
    margin-left:10px;
    font-size:13px;
    font-size:600;
    margin-top:15px;
}

.descArea.center a:hover{
    text-decoration: underline;
}

.number{
    display:flex;
}

.numberInput{
    margin-left:10px;
}

.checkbox{
    display:flex;
    margin-bottom:42px;
}

.checkbox input{
   opacity: 0;
}

.checkbox input + span{
    display:block;
    width:15px;
    height:15px;
    border: 1px solid #E3E3E3;
    box-sizing: border-box;
    border-radius: 4px;
    background-color:#fff;
    margin-left:-15px;
}

.checkbox input:checked + span{
    background:#fff url(data:image/png;base64,) no-repeat center center;
    background-size:100%;
}

.checkbox label{
    margin-left:5px;
    font-family: Comfortaa;
    font-weight: 300;
    font-size: 10px;
    line-height: 13px;
    letter-spacing: -0.01em;
    color: #B7B7B7;
}

.checkbox a{
    color: #0BBDC8;
}

.checkbox a:hover, .checkbox a:focus, .checkbox a:active{
    text-decoration: underline;
}
