#products{
    width:100%;
}

.products_setcion1{
    display:flex;
    flex-direction: row-reverse;
    height:847px;
    background:#fff url('../images/shape1.png') no-repeat;
    background-size:auto 103.5%;
    overflow: hidden;
    background-position:top -30px right -30px;
}

.products_section1_textArea{
    position:relative;
    margin-left:80px;
    width:30%;
}

#products .texts{
    position:absolute;
    top:50%;
    left:0;
    transform: translateY(-50%);
    margin-left:0;
}

.products_title span{
    font-weight: 300;
    font-size: 56px;
    line-height: 69px;
    letter-spacing: -0.01em;
    color: #000000;
    display:block;
}

.products_title span.sub{
    font-size: 36px;
    margin:20px 0;
}

.desc span{
    display:block;
    font-size: 17px;
    line-height: 33px;
    letter-spacing: -0.01em;
}

.products_section1_img{
    flex:1;
    /* display: flex; */
    justify-content: space-around;
    position:relative;
    margin-right:80px;
}

.product_1, 
.product_2,
.product_3{
    position:absolute;
    top:50%;
    transform: translateY(-50%);
}

.product_1{
    right:36%;
    width: 40%;
}

.product_2{
    top:41%;
    right:10%;
    transform: translate(-50%, -50%);
    width:17%;
}

.product_3{
    top:64%;
    right:0;
    width:17%;
}

.products_setcion2{
    display:flex;
    flex-direction: row-reverse;
    background:#F3E5D6 url('../images/shape2.png') no-repeat;
    background-size:auto 100%;
    background-position:bottom 0 left -89px;
    height:852px;
}

.products_section2_textArea{
    width:40%;
}

.products_section2_img{
    flex:1;
    position:relative;
    margin-left:80px;
}

.products_section2_img .img{
    width:18%;
    position:absolute;
    top:50%;
    transform: translateY(-50%);
}

.products_section2_img .img.product_4{
    width:45%;
    left: 35%;
}

.products_section2_img .img.product_5{
    left: 27%;
    transform: translate(-50%, -50%);
}

.products_section2_img .img.product_6{
    left:0;
}

.products_section2_textArea{
    position:relative;
    width:40%;
    height:100%;
}

.products_section2_textArea .text{
    position:absolute;
    top:50%;
    transform: translateY(-50%);
    right: 80px;
}

.products_setcion3{
    padding:0 80px;
}

.products_setcion3 .products_title{
    padding:80px 0 20px;
}

.products_setcion3 .products_title span,
.products_setcion3 .desc span{
    text-align: center;
}

.products_setcion3 .mainImg{
    width:802px;
    margin:125px auto 170px;
}

.info_bottom{
    display:flex;
    justify-content: space-around;
    width:80%;
    margin:0 auto 74px;
}

.info_bottom .img{
    height:42px;
    text-align: center;
}

.info_bottom img{
    width:auto;
    height:100%;
}

.info_bottom .title{
    font-family: comforta;
    font-weight: 400;
    font-size: 39px;
    letter-spacing: -0.01em;
    color: #000000;
    margin:45px auto 5px;
    text-align: center;
}

.info_bottom .text{
    font-size: 13px;
    text-align: center;
    letter-spacing: -0.01em;
    color: #000000;
    font-weight: 300;
}

.desc.product3_desc_mobile{
    display:none;
}

@media only screen and (max-width: 1355px){
    .products_setcion1,
    .products_setcion2{
        display:block;
        width:100%;
    }

    .products_setcion1{
        background-position:top -10vw right -10vw;
        background-size:74%;
    }

    .products_section1_img{
        height: 69%;
        background-size: auto 100%;
        width: 80%;
        margin-left: auto;
    }

    .product_3 {
        top: 65%;
        width: 15%;
    }

    .product_2 {
        top: 50%;
        width: 15%;
        right: 8%;
    }

    .product_1 {
        top: 50%;
        width: 32%;
        right: 31%;
    }   

    .products_section1_textArea{
        width:100%;
    }

    .products_section2_textArea{
        height:auto;
        width:100%;
    }

    .products_section2_textArea .text{
        right:80px;
        left:auto;
        top:80px;
        transform: translate(0,0);
    }

    .products_setcion2{
        background-position:bottom 0px left -30px;
        background-size:580px;
    }

    
    .products_section2_img{
        height: 100%;
        width: 600px;
    }

    .products_section2_img .img{
        transform: translate(0,0) !important;
        top:auto;
        bottom:80px;
    }

    .products_section2_img .img.product_6{
        left:0;
    }

    .products_section2_img .img.product_5{
        left:106px;
    }

    .products_section2_img .img.product_4{
        left: 215px;
        bottom: 13px;
    }

    .products_setcion3 .mainImg{
        width:100%;
    }
    
    .info_bottom{
        width:100%;
    }
}

@media only screen and (max-width: 900px){
    .products_setcion2 .desc,
    .products_setcion3 .desc{
        display:none;
    }

    .products_setcion2 .desc.product2_desc_mobile,
    .products_setcion3 .desc.product3_desc_mobile{
        display:block;
    }

    .products_section1_img{
        margin-right:50px;
    }

    .products_setcion1 {
        background-position: top -50px right -50px;
        background-size: 562px;
        height:720px;
    }

    .products_section1_img{
        height:547px;
    }
    
    .products_title span{
        font-size: 50px;
        line-height: 55px;
    }

    .products_title span.sub {
        font-size: 33px;
        margin: 14px 0;
    }

    .info_bottom{
        display:block;
    }

    .info_bottom li{
        margin-bottom:100px;
    }

    .info_bottom .title{
        font-weight: normal;
        font-size: 30px;
        margin-bottom:25px;
    }
    
}

@media only screen and (max-width: 767px){
    .products_setcion3 {
        padding: 0 50px;
    }

    .product_3 {
        width: 17%;
    }

    .product_2 {
        width: 17%;
        right: 10%;
    }

    .product_1 {
        width: 36%;
        right: 36%;
    } 
}

@media only screen and (max-width: 644px){
    .products_title span {
        font-size: 45px;
        line-height: 52px;
    }

    .products_title span.sub {
        font-size: 30px;
        margin: 8px 0 13px;
    }

    .desc span {
        font-size: 15px;
        line-height: 28px;
    }

    .products_setcion1 {
        background-position: top 19px right -50px;
        background-size: 431px;
    }

    .products_section1_img {
        width:415px;
    }

    .product_1{
        width: 45%;
        right: 40%;
    }

    .product_2 {
        right: 10%;
        width: 21%;
        top:45%;
    }

    .product_3 {
        right: 0;
        width: 21%;
        top: 65%;
    }

    .products_setcion2 {
        background-size: 454px;
    }

    .products_section1_textArea .texts{
        margin-left:0;
    }

    .products_section2_textArea .text{
        left:50px;
        right:auto;
    }

    .products_section2_img{
        width:100%;
        margin-left:0;
    }

    .products_section2_img .img {
        bottom: 119px;
    }

    .products_section2_img .img.product_6 {
        left: 47px;
        width: 94px;
    }

    .products_section2_img .img.product_5 {
        left: 142px;
        width: 94px;
    }

    .products_section2_img .img.product_4 {
        left: 233px;
        width: 216px;
        bottom: 80px;
    }
}

@media only screen and (max-width: 468px){
     .products_setcion1 {
        background-position: top 0px right -100px;
        background-size: 411px;
        height:640px;
    }

    .products_section1_img {
        width: 300px;
        margin: 0 auto;
        height: 77%;
    }

    .product_1 {
        width: 176px;
        left: -30px;
        right: auto;
    }
    .product_2 {
        left: auto;
        right: 22px;
        width: 83px;
        top:44%;
    }

    .product_3 {
        right: -29px;
        width: 83px;
        top: 60%;
    }

    .products_section1_textArea{
        margin-left:30px;
    }

    .products_title span {
        font-size: 30px;
        line-height: 45px;
    }

    .products_title span.sub {
        font-size: 20px;
        margin:5px 0 14px;
    }

    .desc span{
        font-size: 12px;
        line-height: 21px;
    }


    .products_setcion2{
        height:640px;
        background-size: 337px;
    }


    .products_section2_textArea .text {
        right: auto;
        left:30px;
        top:49px;
    }

    .products_section2_img{
        width:320px;
        margin:0 auto;
    }

    .products_section2_img .img {
        bottom: 79px;
    }

    .products_section2_img .img.product_6 {
        left: 0;
        width: 78px;
    }

    .products_section2_img .img.product_5 {
        left: 84px;
        width: 78px;
    }

    .products_section2_img .img.product_4 {
        left: auto;
        right:-20px;
        width: 181px;
        bottom: 43px;
    }

    .products_setcion3 .products_title {
        padding: 49px 0 20px;
    }

    .products_setcion3 .mainImg {
        margin: 51px auto 100px;
    }

    .info_bottom li {
        margin-bottom: 65px;
    }

    .info_bottom .img {
        height: 34px;
    }

    .info_bottom .title {
        font-size: 25px;
        margin-top: 35px;
    }
}