#company{
    width:100%;
}

.companysetcion1{
    height:855px;
    background:#F3E5D6 url('../images/company1_1.png')  no-repeat bottom right;
    background-size:auto 100%;
    display:flex;
    overflow: hidden;
}

.company1_img{
    position:relative;
    height:855px;
    width:100%;
}

.company1_img img{
    width:60%;
    position:absolute;
    bottom:0;
    right:20%;
}

.company_section1_textArea{
    background: url('../images/company1.png')  no-repeat bottom left;
    background-size:auto 100%;
    position:relative;
    width:100%;
    height:70%;
    top:15%;
}

.company_section1_textArea .texts{
    position:absolute;
    top:50%;
    left:0;
    transform: translateY(-50%);
    margin-left:80px;
    z-index: 2;
}

.companys_section2_textArea .texts{
    position:absolute;
    top:50%;
    right:80px;
    transform: translateY(-50%);
}

.title span{
    font-weight: 300;
    font-size: 56px;
    line-height: 69px;
    letter-spacing: -0.01em;
    color: #000000;
    display:block;
}

.title span.sub{
    font-size: 36px;
    margin:20px 0;
}

.companys_setcion2{
    margin-top:70px;
    height:855px;
    background:#fff url('../images/company3.png')  no-repeat;
    background-size:auto 100%;
    background-position:bottom 0 left 80px;
}

.companys_section2_textArea{
    margin-left:auto;
    width:40%;
    position: relative;
    height:100%;
}

.partners{
    width:80%;
    height:845px;
    margin:0 auto;
}

.partners h2{
    font-family: Comfortaa;
    font-weight: 300;
    font-size: 45px;
    line-height: 69px;
    text-align: center;
    letter-spacing: -0.01em;
    margin-top:145px;
}

.partners ul{
    margin-top:180px;
    display:grid;
    grid-template-columns: repeat(3, 33%);
    grid-template-rows: repeat(2, 40px);
    gap:175px 0;
}

.partners li{
    text-align: center;
}

.partners li img{
    width:auto;
    height:100%;
}

.company_bobile_desc{
    display:none;
}

@media only screen and (max-width: 1500px){
    .company1_img img{
        width:70%;
    }

    .companys_setcion2{
        background-size: 43% auto;
        background-position: center left 80px;
    }
}

@media only screen and (max-width: 1205px){
    .companysetcion1{
        height:640px;
        background-size: auto 80%;
        /* display:flex; */
    } 

    .company_section1_textArea {
        height:400px;
        top: 115px;
    }

    .company1_img{
        height:640px;
    }

    .company1_img img{
        width:300px;
    }

    #company .desc{
        display:none;
    }

    #company .desc.company_bobile_desc{
        display:block;
    }

    .title span{
        font-size:45px;
        line-height: 55px;
    }

    .title span.sub{
        font-size:25px;
        line-height:32px;
    }

    #company .desc span{
        font-size: 14px;
        line-height: 25px;
    }

    .companys_setcion2{
        background-size:50% auto;
        height:640px;
        background-position: bottom left 80px;
    }

    .companys_section2_textArea{
        width:100%;
    }

    .companys_section2_textArea .texts{
        top:0;
        transform: translateY(0);
    }

    .partners ul{
        grid-template-columns: repeat(2, 50%);
        grid-template-rows: repeat(3, 50px);
    }
}

@media only screen and (max-width: 767px){
    .companysetcion1{
        display:block;
    }

    .company_section1_textArea{
        background-position:top 0 left -50px;
    }

    .company_section1_textArea .texts{
        margin-left:50px;
    }
    
    .company1_img{
        height:242px;
    }

    .company1_img img{
        width:auto;
        height:100%;
    }
    
    .companysetcion1{
        background-position:bottom -100px right -30px;
    }

    .companys_setcion2{
        padding:0 50px;
        background-position:bottom center;
        background-size:300px auto;
        height:601px;
    }

    .companys_section2_textArea .texts{
        right:auto;
        left:0;
    }

    .partners ul{
        grid-template-columns: repeat(1, 100%);
        grid-template-rows: repeat(6, 30px);
        gap: 80px 0;
        margin-top:100px;
    }

}

@media only screen and (max-width: 600px){
    .companysetcion1{
        height: 682px;
    }

    .company_section1_textArea{
        height:330px;
        background-position: top 0 left -94px;
    }

    .title span{
        font-size: 30px;
        line-height: 45px;
    }

    .title span.sub{
        font-size: 20px;
        line-height: 45px;
        margin:10px 0;
    }

    #company .desc span{
        font-size: 12px;
        line-height: 21px;
    }

    .company1_img {
        height: 242px;
        top: 112px;
    }

    .company1_img img{
        left:50%;
        transform: translateX(-50%);
    }

    .partners h2{
        margin-top:100px;
        font-size: 30px;
        line-height: 85px;
    }

    .partners ul{
        grid-template-rows: repeat(6, 22px);
        gap: 61px 0;
        margin-top: 80px;
    }

    .partners{
        height:675px;
    }
}