.terms{
    padding:80px;
}

.term_title{
    margin-bottom:98px;
    text-align: center;
}

.terms h2{
    font-family: Comfortaa;
    font-weight: 300;
    font-size: 45px;
    line-height: 69px;
    text-align: center;
    letter-spacing: -0.01em;
    margin-bottom:20px;
}

.terms .sub{
    font-family: Comfortaa;
    font-size: 15px;
    line-height: 25px;
    text-align: center;
    letter-spacing: -0.01em;
    color: #B7B7B7;
}

.info p{
    font-family: Spartan;
    font-size: 15px;
    line-height: 25px;
    letter-spacing: -0.01em;
    color: #1C1C1C;
    margin-bottom:20px;
}

.info h3{
    font-family: Spartan;
    font-weight: bold;
    font-size: 15px;
    line-height: 25px;
    letter-spacing: -0.01em;
    color: #1C1C1C;
}

.more_btn{
    cursor: pointer;
    font-family: Comfortaa;
    font-weight: bold;
    font-size: 17px;
    line-height: 25px;
    letter-spacing: -0.01em;
    color: #0BBDC8;
}

@media only screen and (max-width: 767px){
    .terms{
        padding:80px 50px;
    }

    .terms h2{
        font-size: 40px;
        line-height: 85px;
    }

    .term_title {
        margin-bottom: 75px;
    }
}

@media only screen and (max-width: 468px){
    .terms{
        padding:50px 20px;
    }

    .terms h2{
        font-size: 30px;
        line-height: 85px;
        margin-bottom:0;
    }

    .terms .sub{
        font-size: 12px;
        line-height: 21px;
    }

    .term_title {
        margin-bottom: 45px;
    }

    .info p{
        font-size: 11px;
        line-height: 20px;
    }

    .more_btn{
        font-size: 12px;
        line-height: 25px;
    }

    .info h3{
        font-size:13px;
    }
}